@tailwind base;
html {
  font-size: 62.5%;
}

body {
  @apply text-black;
  font-size: 1.6em;
  font-family: "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", "Hiragino Sans", Meiryo, sans-serif;
}

h2 {
  @apply font-bold;
  font-size: 20px;
  padding-bottom: 7px;
}

h3 {
  @apply font-bold pb-1;
}

@tailwind components;
#root {
  @apply min-h-screen;
  @apply grid;
  grid-template-rows: 50px 1fr 30px;
}

#app-header {
  @apply bg-main-dark text-white;
  @apply w-full;
  @apply font-bold;
  padding: 10px 20px 5px 20px;
  font-size: 24px;
}

#app-container {
  @apply grid;
  grid-template-columns: 180px 1fr;
}
#app-container nav {
  @apply bg-main text-white;
  padding-top: 4px;
  padding-left: 20px;
}
#app-container nav header {
  @apply font-bold;
}
#app-container nav > ul > li {
  padding: 5px 0;
}
#app-container nav > ul > li > ul {
  padding-left: 10px;
}
#app-container main {
  @apply py-6 px-12;
}

#app-footer {
  @apply bg-main-dark text-white;
  @apply flex items-center;
  @apply pl-4;
}

.single-column-form {
  @apply flex flex-col;
  max-width: 600px;
}

.horizontal-container {
  @apply py-4;
}

.horizontal-items {
  @apply flex;
}
.horizontal-items > :not(:first-child) {
  margin-left: 15px;
}
.horizontal-items.right {
  @apply justify-end;
}

hr.border {
  @apply border-0 border-t text-gray-dark;
}

.fullwidth {
  @apply w-full;
}

.overlay {
  @apply relative;
}
.overlay::after {
  @apply block absolute top-0 left-0;
  @apply w-full h-full;
  content: "";
}

.button-base, .button-link, .button-primary, .button {
  @apply px-6 py-px;
}

.button {
  @apply border-2 border-gray-dark border-solid;
}
.button:hover {
  @apply bg-gray text-white;
}

.button-primary {
  @apply border-2 border-accent border-solid;
  @apply text-accent;
}
.button-primary:hover {
  @apply bg-accent-dark text-white;
}

.button-link {
  @apply border-none;
  @apply text-main-dark;
}
.button-link.shrink {
  @apply px-0;
}
.button-link:hover {
  @apply text-main-hover;
}

.table th {
  padding: 8px 18px;
}
.table thead th {
  @apply bg-main-dark text-white;
  @apply border-b border-white;
}
.table thead th:not(:first-child) {
  @apply border-l border-white;
}
.table tbody th {
  @apply bg-main-light text-main-dark;
  @apply border-r border-main-dark;
}
.table td {
  padding: 6px 10px;
}
.table tr {
  @apply border border-main-dark;
}

.form-item {
  @apply flex flex-col;
  padding: 2px 15px 10px 0;
}
.form-item > label,
.form-item > legend {
  @apply font-bold pb-1;
}
.form-item > label + *,
.form-item > legend + * {
  min-height: 30px;
}

.field-validation-error {
  @apply text-attention;
}

input,
textarea,
select {
  @apply border border-gray-dark rounded-sm;
}
input:disabled,
textarea:disabled,
select:disabled {
  @apply bg-white text-gray-light border-gray-light;
}

input,
select {
  @apply h-12;
}

input[type=text],
input[type=password],
input[type=search],
input[type=tel],
input[type=url],
input[type=email],
input[type=date],
input[type=time],
input[type=number],
input[type=range],
input[type=color],
input[type=datetime],
input[type=datetime-local],
input[type=month],
input[type=week],
textarea,
select {
  @apply w-full;
  padding: 3px 10px;
}

input[type=number] {
  padding: 3px;
}

input[type=checkbox],
input[type=radio] {
  @apply relative;
  @apply outline-none cursor-pointer;
  top: 4px;
  height: 20px;
  width: 20px;
}
input[type=checkbox] ~ span,
input[type=radio] ~ span {
  @apply cursor-pointer;
  @apply ml-2;
}
input[type=checkbox]:before,
input[type=radio]:before {
  @apply block absolute pointer-events-none;
  @apply border-solid border-2 border-main;
  content: "";
  height: 20px;
  width: 20px;
}
input[type=checkbox]:after,
input[type=radio]:after {
  @apply block absolute pointer-events-none;
  content: "";
}
input[type=checkbox]:checked:after,
input[type=radio]:checked:after {
  @apply bg-main;
}
input[type=checkbox]:disabled,
input[type=radio]:disabled {
  @apply cursor-auto;
}
input[type=checkbox]:disabled ~ span,
input[type=radio]:disabled ~ span {
  @apply cursor-auto;
  @apply text-gray-light;
}
input[type=checkbox]:disabled:checked:after,
input[type=radio]:disabled:checked:after {
  @apply bg-gray-light;
}
input[type=checkbox]:disabled:before,
input[type=radio]:disabled:before {
  @apply border-gray-light;
}

input[type=checkbox]:after {
  height: 20px;
  width: 20px;
}
input[type=checkbox]:checked:after {
  background-image: url(/image/check.png);
  background-position: center;
  background-repeat: no-repeat;
}

input[type=radio]:before, input[type=radio]:after {
  @apply rounded-full;
}
input[type=radio]:after {
  top: 4px;
  left: 4px;
  height: 10px;
  width: 10px;
}

.wysiwyg-styles {
  @apply text-left;
}
.wysiwyg-styles .trumbowyg-box {
  @apply my-0;
}
.wysiwyg-styles h3 {
  @apply text-3xl;
}
.wysiwyg-styles strong {
  @apply font-bold;
}
.wysiwyg-styles a {
  @apply underline;
  color: #0000ee;
}
.wysiwyg-styles ul {
  @apply list-disc pl-12;
}
.wysiwyg-styles ol {
  @apply list-decimal pl-12;
}

.pagination {
  @apply flex flex-row;
}
.pagination span {
  height: 30px;
  width: 30px;
  line-height: 30px;
}
.pagination span:not(:first-child) {
  @apply ml-2;
}
.pagination span * {
  @apply w-full h-full;
}
.pagination span.page {
  @apply border border-main rounded-sm;
  @apply text-main text-center align-baseline;
}
.pagination span.page.selected {
  @apply bg-main text-white;
}

.search-form {
  @apply border-t border-b border-gray;
  @apply py-3;
}
.search-form .search-item {
  @apply flex items-center;
}
.search-form .search-item:not(:first-child) {
  @apply ml-8;
}
.search-form .search-item label,
.search-form .search-item span {
  @apply whitespace-no-wrap;
}
.search-form .search-item > :not(:first-child) {
  margin-left: 10px;
}

.dialog {
  display: none;
  box-sizing: border-box;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 300px;
  min-height: 200px;
  background-color: white;
  z-index: 99999;
  padding: 20px;
}
.dialog.open {
  display: block;
}

.dialog-background {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(128, 128, 128, 0.3);
  z-index: 99998;
}
.dialog-background.closed {
  display: none;
}

@tailwind utilities;